#Reciept {
    display: flex;
    align-items: center;
    width: 100%;
    margin: 10px 0px 10px 0px;

}

span {
    cursor: pointer;
    padding: 10px;
    border-radius: 2px;
    margin-right: 10px;
    transition: background-color 0.3s, color 0.3s;
}

#Original {
    background-color: #71b944;
    color: white;
}

#Original:hover {
    background-color: #55893f;
}

.Progress {
    background-color: #fdb939;
    color: white;
}

.Progress:hover {
    background-color: #d68c03;
}

.Ackslip {
    background-color: #249cec;
    color: white;
    float: left;
    margin-top: 5px;
}

.Ackslip:hover {
    background-color: #267bbf;
}


.pencil {
    background-color: #71b944;
    color: white;
    padding: 10px;
    font-size: 20px;

    border-radius: 3px;
    
}

.Action{
    display: flex;
        display: flex;
            margin-top: 4rem;
}

h3{
        margin-top: 10px;
}